import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { SharedModule } from '@shared/shared.module';

import { DashboardComponent } from './dashboard.component';
import { DashboardContainerComponent } from './dashboard-container.component';
import { DashboardIntroComponent } from './dashboard-intro.component';

const routes: Routes = [
  { path: '', pathMatch: 'full', component: DashboardContainerComponent }
];

@NgModule({
  imports: [SharedModule, RouterModule.forChild(routes)],
  declarations: [DashboardComponent, DashboardContainerComponent, DashboardIntroComponent]
})
export class DashboardModule {}
