import { Component } from '@angular/core';
import { Observable } from 'rxjs';
import { DashboardVm } from './dashboard-vm';
import { DashboardViewService } from './dashboard-view.service';
import { fadeAnimation } from '@animations';

@Component({
  selector: 'sp-dashboard-container',
  template: `
    <div *ngIf="vm$ | async as vm">
      <sp-dashboard-intro *ngIf="vm.showIntro; else dash" [vm]="vm"></sp-dashboard-intro>
      <ng-template #dash>
        <sp-dashboard [vm]="vm" [@fade]></sp-dashboard>
      </ng-template>
    </div>
  `,
  animations: [ fadeAnimation ]
})
export class DashboardContainerComponent {
  vm$: Observable<DashboardVm>;
  constructor(dashboardViewService: DashboardViewService) {
    this.vm$ = dashboardViewService.vm$;
  }
}
